import React, { Component } from "react";
import MuiThemeProvider from "material-ui/styles/MuiThemeProvider";
import { observer } from "mobx-react";
import Styles from "./Styles";
import EBISC from "./EBISC";
import EBISCLogo from "./ebisc.png";
import HIPSCILogo from "./hipsci.png";
import { Container, Row, Col } from "react-grid-system";

@observer
class App extends Component {
  componentDidMount() {}

  render() {
    return (
      <MuiThemeProvider>
        <div>
          <Row style={{ background: "#333", padding: "32px" }}>
            <Col sm={6}>
              <img src={EBISCLogo} />
            </Col>
            <Col style={{ textAlign: "center" }} sm={6}>
              <img src={HIPSCILogo} />
            </Col>
          </Row>

          <div style={Styles.container}>
            <header style={Styles.header}>
              <h1 style={Styles.heading}>
                Allelic Query Service <span style={Styles.subHeading}>UI</span>
              </h1>
            </header>

            <div style={Styles.App} className="App">
              <EBISC />
            </div>
          </div>
        </div>
      </MuiThemeProvider>
    );
  }
}

export default App;



