import Colors from "./Colors";

const styles = {
  searchField: {
    backgroundColor: "#F5F5F5",
    width: "calc(100% - 5px)",
    boxSizing: "content-box"
  },
  App: {
    padding: "32px"
  },
  spacing: {
    padding: "32px",
    margin: "32px 0"
  },
  underlineDark: {
    borderColor: "#B0BEC5"
  },
  underlineDarkFocused: {
    borderColor: "#90A4AE"
  },
  results: { margin: "60px auto", maxWidth: "800px" },
  container: {
    maxWidth: "1280px",
    margin: "62px auto"
  },
  input: {
    padding: "8px 12px",
    borderWidth: 0,
    border: "2px solid #333",
    backgroundColor: "#fff",
    fontSize: "22px",
    color: "#ededed",
    width: "100%"
  },
  header: {
    textAlign: "center",
    padding: "32px"
  },
  heading: {
    color: Colors.fontDark,
    fontSize: "52px",
    margin: 0,
    fontWeight: "300"
  },
  heading2: {
    color: Colors.fontDark,
    fontSize: "38px",
    margin: "32px 0",
    fontWeight: "300"
  },
  subHeading: {
    color: Colors.fontLight,
    fontSize: "28px"
  },
  chipLeft: {
    background: "#7A7A7A",
    padding: "8px",
    margin: "8px 0",
    color: "#e2e2e2",
    borderRadius: "5px",
    display: "inline-block"
  },
  chip: {
    background: "#7A7A7A",
    padding: "8px",
    margin: "8px 0",
    color: "#e2e2e2",
    borderRadius: "5px",
    display: "inline-block",
    float: "right"
  },
  tag: {
    backgroundColor: "#B0BEC5",
    padding: "6px",
    borderRadius: "6px",
    color: "#37474F",
    fontSize: "14px",
    display: "inline-block "
  }
};
export default styles;



