import { observable, computed, action } from "mobx";
const URL = "https://gti-es-0.ebi.ac.uk:8080/api/";
class Store {
  @observable searchText = "";
  @observable selectedSpecies = {};
  //Animation
  @observable slideIndex = 0;
  //Animaion Ends

  @observable userSelection = "VARIANT";
  @observable allelefreq = 5;
  @observable inputType = "";

  @computed
  get inputCategory() {
    if (this.inputType === "dbSNP") {
      return "Variant";
    }

    if (
      this.inputType === "GO" ||
      this.inputType === "Gene Symbol" ||
      this.inputType === "RefSeq"
    ) {
      return "Genes";
    }
    return "Variant";
  }

  @action
  proceed() {
    console.log("PROCEEDING");
    if (this.slideIndex < 2) this.slideIndex += 1;
    else {
      this.slideIndex = 0;
    }
  }

  @action
  fetchStuff(url) {
    //console.log("FETCHING: ", url);
    return fetch(url).then(x => x.json());
  }

  @computed
  get getOpacity() {
    console.log("SLIDE INDEX", this.slideIndex);
    var arr = [0.35, 0.35, 0.35];
    arr.splice(this.slideIndex, 1, 1);
    console.log(arr);
    return arr;
  }

  @computed
  get getTransition() {
    return (
      "translateX(calc(-" +
      1 * this.slideIndex +
      "00% - " +
      64 * this.slideIndex +
      "px))"
    );
    //return "translateX(calc(-100% - 64px))";
  }
}

let store = new Store();
export default store;



